<template>
  <div class="toanswer" v-wechat-title="$route.meta.title">
    <div class="ans-left">
      <ul class="ans-box">
        <li @click="toessay(item.aid)" v-for="(item,index) in answer" :key="index" class="ans-item">
          <div class="ans-title">{{item.title}}</div>
          <div class="ans">
            <img src="@/images/write.png" alt="">
            回答
          </div>
        </li>
      </ul>
    </div>
    <div class="ans-right">
      <userinfo></userinfo>
     <div class="box" style="margin-top: 20px;">
       <recommend></recommend>
     </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "toanswer",
    data() {
      return {
        answer: [],
      }
    },
    mounted() {
      this.getarticle();
    },
    methods: {
      //  跳往页面详情
      toessay(aid) {
        this.$router.push({path: '/essay', query: {aid}})
      },
      //获取文章数据
      getarticle() {
        this.$post("index/data/question_list").then(res => {
          this.answer = res.data;
        }).catch(err => {
          console.log(err);
        })
      },
    }
  }
</script>

<style scoped lang="scss">
  .toanswer {
    width: 1180px;
    margin: 40px auto 0;
    display: flex;
    justify-content: space-between;

    .ans-left {
      width: 788px;
      .ans-box {
        padding: 0 20px;

        .ans-item {
          cursor: pointer;
          width: 100%;
          height: 84px;
          display: flex;
          justify-content: center;
          flex-direction: column;

          .ans-title {
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            width: 100%;
            font-size: 14px;
            font-weight: 400;
            color: rgba(51, 51, 51, 1);
            line-height: 40px;
          }

          .ans {
            img {
              width: 15px;
              height: 15px;
              margin-right: 10px;
            }
          }
        }
      }
    }

    .ans-right {
      width: 350px;
    }
  }

</style>



